
.DelaysInDeliveryWrapper{
    display: grid;
    grid-template-columns: 2vw 82vw auto;
    grid-template-rows: 7vh 15vh 5vh auto;
    grid-template-areas: 
    "header header header "
    ". filterButtons ."
    ". . ."

    "chartColors chart .";
    
    background-color: #C8C9C7;
  }
.DelaysInDelivery__Table{
  grid-area:Table;
}
  .chart_Colors{
    margin-left: 10px;
    /* display: flex; */
    grid-area: chartColors;
    justify-content: center;
    justify-content: space-around;
  }
  .chart_Colors_Status{
    margin-bottom: 5px;
    font:bolder;
    font-size: 20px;
    color:white;
    background-color: #D9514EFF;
    width: fit-content;
    height: fit-content;
    border-radius: 5px;
  }
  .chart_Colors_Priority{
    margin-bottom: 5px;
    font: bolder;
    font-size: 20px;
    color: white;
    background-color: #2A2B2DFF;
    width: fit-content;
    height: fit-content;
    border-radius: 5px;
  }
  .chart_Colors_QaRepresentative{
    margin-bottom: 5px;
    font: bolder;
    font-size: 20px;
    color: white;
    background-color: #2DA8D8FF;
    width: fit-content;
    height: fit-content;
    border-radius: 5px;
  }
  .DelaysInDelivery__Title{
    font-weight: 500;
    grid-area: header;
    text-align: center;
    color: #00447C;
    background-color: white;
    height: 100%;
    font-family: 'Roboto Slab', serif;
    font-size: 26px;
    padding-top: 2vh;
  }
  .DelaysInDelivery__Filters__wrapper {
    
    grid-area: filterButtons;
    display: grid;
    height: 7rem;
    background-color: #eeeeee;
    align-items: center;
    text-align: center;
    padding-left: 1rem;
    padding-right: 1rem;
    border-radius: 3px;
    margin-top: 1rem;
    padding-bottom: 1rem;
    padding-top: 1rem;
  }
  
  .DelaysInDelivery__Filters__headers{
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    color: #0076ce;
    text-shadow: 0 .1rem .3rem#00447c28;
  }
  
  .filter__header__item{
    justify-content: space-between;
    width: 12rem;
    /* background-color: red; */
  }
  
  .DelaysInDelivery__Filters__fields {
    display: flex;
    justify-content: space-between;
  }
  
  
  .DelaysInDelivery__Filter{
   width: 13rem;
   /* border: 0.1px solid black; */
   box-sizing: border-box;
   background-color:   #EEEEEE;
   color: black;
   box-shadow: 0 .1rem .3rem rgba(0, 0, 0, 0.2);
  }

  .DelaysInDelivery__Filter__date{
    box-shadow: inset 0px 0.1rem 0.3rem rgba(0, 0, 0, 0.2);
    border: .1px solid rgba(0, 0, 0, 0.2);
    height:2.25rem;
    width: 13rem;
    border-radius: 3px;
    color: rgba(0, 0, 0, 0.7);
    text-align: center;
    font-size: 15px;
  }
  
  .DelaysInDelivery__Chart{
    grid-area: chart;
  }

  .DelaysInDelivery__Filters__Header > p {
    text-align: center;
    color: #00447C;
    font-weight: bold;
  }

  

  